<template>
  <div class="tradingCenter" id="tradingCenter">
    <el-container>
      <el-main>
        <el-row>
          <el-col :span="4">
            <el-aside width="200px">
              <el-radio-group v-model="isCollapse" style="margin-bottom: 2px;">
                <el-radio-button :label="false">展开</el-radio-button>
                <el-radio-button :label="true">收起</el-radio-button>
              </el-radio-group>
              <el-menu class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" :router="true">
                <el-submenu index="receivables">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">收款</span>
                  </template>
                  <el-menu-item index="/tradingcenter/gateway">网关管理</el-menu-item>
                  <el-menu-item index="/tradingcenter/quickpayment">快捷支付管理</el-menu-item>
                  <el-menu-item index="/tradingcenter/consignmentcollection">委托收款管理</el-menu-item>
                  <el-menu-item index="/tradingcenter/scancode">扫码管理</el-menu-item>
                </el-submenu>
                <el-submenu index="payment">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">付款</span>
                  </template>
                  <el-menu-item index="/tradingcenter/paymentorder">付款订单</el-menu-item>
                  <el-menu-item index="/tradingcenter/paymenttransaction">付款交易</el-menu-item>
                  <el-menu-item index="/tradingcenter/paymenttransactionbatch">付款交易批次</el-menu-item>
                  <el-menu-item index="/tradingcenter/transactionpack">交易手工打包</el-menu-item>
                </el-submenu>
                <el-submenu index="authentication">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">鉴权</span>
                  </template>
                  <el-menu-item index="/tradingcenter/authenticationorderflow">鉴权订单流水</el-menu-item>
                  <el-menu-item index="/tradingcenter/authenticationtransaction">鉴权交易</el-menu-item>
                </el-submenu>
                <el-submenu index="electroniccontract">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">电子合同</span>
                  </template>
                  <el-menu-item index="/tradingcenter/electronicsignatureorder">电子签名订单</el-menu-item>
                  <el-menu-item index="/tradingcenter/electronicsignaturedata">电子签名资料</el-menu-item>
                  <el-menu-item index="/tradingcenter/electronicsignaturetransaction">电子签名交易</el-menu-item>
                  <el-menu-item index="／tradingcenter/electronicchapterorder">电子签章订单</el-menu-item>
                </el-submenu>
                <el-submenu index="accountservice">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">账户服务</span>
                  </template>
                  <el-menu-item index="/tradingcenter/accountpayment">账户支付管理</el-menu-item>
                  <el-menu-item index="/tradingcenter/accountwithholding">账户代扣管理</el-menu-item>
                  <el-menu-item index="/tradingcenter/accounttransfer">账户转账管理</el-menu-item>
                </el-submenu>
                <el-submenu index="split">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">分账</span>
                  </template>
                  <el-menu-item index="/tradingcenter/splitorder">分账订单</el-menu-item>
                  <el-menu-item index="/tradingcenter/splitdetailed">分账明细</el-menu-item>
                </el-submenu>
                <el-submenu index="refund">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">退款</span>
                  </template>
                  <el-menu-item index="/tradingcenter/refundtobeaudited">退款待审核</el-menu-item>
                  <el-menu-item index="/tradingcenter/refundtoreviewacase">退款复审</el-menu-item>
                  <el-menu-item index="/tradingcenter/refundpaymendetails">退款代付明细</el-menu-item>
                  <el-menu-item index="／tradingcenter/refunddetailed">退款明细</el-menu-item>
                </el-submenu>
                <el-submenu index="ransactionlog">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">交易日志</span>
                  </template>
                  <el-menu-item index="/tradingcenter/gatewayservicelog">网关服务日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/quickpaymentlog">快捷支付日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/consignmentcollection">扫码日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/scancodelog">委托代收日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/payforanotherlog">委托代付日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/authenticationlog">鉴权日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/electroniccontractlog">电子合同日志</el-menu-item>
                  <el-menu-item index="/tradingcenter/accountservicelog">账户服务日志</el-menu-item>
                </el-submenu>
                <el-submenu index="exceptionorder">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">异常订单</span>
                  </template>
                  <el-menu-item index="/tradingcenter/exceptionorder">异常订单</el-menu-item>
                </el-submenu>
                <el-submenu index="merchantnotification">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">商户通知</span>
                  </template>
                  <el-menu-item index="/tradingcenter/merchantnotificationlist">商户通知列表</el-menu-item>
                </el-submenu>
              </el-menu>
            </el-aside>
          </el-col>
          <el-col :span="20">
            <el-breadcrumb separator="/" style="padding:20px;">
              <template v-for="route in routeList">
                <!--<span>{{!route.path?'/':route.path.match(/:/)?route.path.replace(/:\S+/,''):route.path}},</span>-->
                <el-breadcrumb-item :to="{path:!route.path?'/':route.path.match(/:/)?route.path.replace(/:\S+/,''):route.path}">{{route.name}}</el-breadcrumb-item>
              </template>
            </el-breadcrumb>
            <router-view></router-view>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'tradingCenter',
    data() {
      return {
        activeName: '',
        isCollapse: false,
        routeList:[]
      }
    },
    created(){
      this.$set(this,'routeList',this.$route.matched)
    },
    methods: {
      handleClick(tab, event) {

      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-header, .el-footer {
    /*background-color: #B3C0D1;*/
    color: #333;
    background-color: #3fbc9c;
    text-align: left;
  }

  .el-aside {
    overflow: inherit;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .el-button {
    height: 60px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-radio-group {
    display: inherit;
  }
</style>



